@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
*{box-sizing:border-box}body{font-size:1.5rem;font-family:"Roboto",sans-serif;color:#000;overflow-wrap:break-word}h1,h2,h3,h4,h5,h6{font-weight:900;max-width:40rem}h1{font-size:3.3rem}h2{font-size:2.5rem}h3{font-size:2.3rem}h4{font-size:1.5rem}h5{font-size:1.3rem}h6{font-size:1rem}code{background-color:#eee;padding:.5rem;display:block}p{line-height:1.8rem}a,.link{font-size:1.5rem;color:#04aee7;text-decoration:underline;transition:none}::-webkit-input-placeholder{color:#a5a39d}:-ms-input-placeholder{color:#a5a39d}::placeholder{color:#a5a39d}.element{padding:2rem;margin-bottom:2.5rem;border-top-width:.15rem;border-top-style:solid;border-top-color:#fff;background:rgba(0,0,0,.75)}#interactive-elements,#combined-elements{margin-top:5rem}li{line-height:1.8rem;margin-top:1rem;margin-bottom:1rem}footer{margin-top:5rem;padding-bottom:2.5rem}.navbar{background:#004833}.nav-props{text-decoration:none;cursor:pointer;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;font-style:normal;letter-spacing:0em;display:block;outline:none}.nav-props:hover{text-decoration:none}.site-title{font-weight:700;font-style:normal;font-size:2.5rem;color:#fff}.site-title:hover{color:#fff}.nav-link-button{color:#fff;padding:.2rem 0;margin-left:.5rem;margin-right:.5rem;font-size:2rem;font-weight:500;line-height:1.8rem}.underline{background-repeat:repeat-x;background-image:linear-gradient(to right, #ffffff 100%, #ffffff 0);background-size:1px 1px;background-position:0 calc(100%)}.btn{font-weight:300}.btn:focus{outline:none;box-shadow:none}.navbar-toggler{outline:none;border:none}#container{display:flex;min-height:100vh;padding-top:10rem;flex-direction:column}#main-content{flex:1 1}
